.popular-movies{
  display: grid;
  margin-top: 0.5rem;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  grid-column-gap: 0.5rem;
  grid-row-gap: 0.5rem;
}

img{
  width: 70%;
  height: 35vh;
  object-fit: cover;
  border-radius: 1rem;
  margin-bottom: 1rem;
}

h2{
  font-size: 0.8rem;
}

.filter-container button{
  margin-right: 2rem;
  min-width: 5rem;
  padding: 0.2rem 0.2rem;
  border: none;
  background: white;
  color: rgb(65,98,168);
  border-radius: 1rem;
  border: 2px solid rgb(65,98,168);
  font-weight: bold;
  cursor: pointer;
}

button.active{
  background: rgb(65,98,168);
  color: white;
}

